	module.exports = {"data":{"markdownRemark":{"headings":[],"html":"<p>For post secondary students looking to lead a healthy and happy lifestyle while attending school, Balance magazine is a reliable resource containing only student speci c health and  tness related content.</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 1500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 66.64999999999999%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"floto jennifer2 digital 002\"\n        title=\"\"\n        src=\"/the-next-website/static/floto_jennifer2_digital_002-afbf72bb001ea75b09b3c5c75e9f0f00-35578.jpg\"\n        srcset=\"/the-next-website/static/floto_jennifer2_digital_002-afbf72bb001ea75b09b3c5c75e9f0f00-21994.jpg 375w,\n/the-next-website/static/floto_jennifer2_digital_002-afbf72bb001ea75b09b3c5c75e9f0f00-f7c9d.jpg 750w,\n/the-next-website/static/floto_jennifer2_digital_002-afbf72bb001ea75b09b3c5c75e9f0f00-35578.jpg 1500w,\n/the-next-website/static/floto_jennifer2_digital_002-afbf72bb001ea75b09b3c5c75e9f0f00-5ea4e.jpg 2000w\"\n        sizes=\"(max-width: 1500px) 100vw, 1500px\"\n      />\n    </span>\n  </span>\n  </p>\n<p>As part of an Editorial course, the objective was to pick a topic of your choosing and find three articles to form the content of the magazine. One of the articles was to be represented strictly using typography, one using typography and illustrations, and the last using typography and photography.  Sprung from personal experiences my topic of choosing was the mental and physical health of post-secondary students. My personal objective was to design a reliable resource providing guidance and support to those looking to lead a healthy lifestyle during their time at school. I know from personal experience that the combination of stress from a full course load, and multiple emotions felt from being away from home and your support system can take a big toll on both your physical and mental health.</p>","frontmatter":{"title":"Balance Magazine","graduate":"Jennifer Floto","category":"Editorial Design","category2":null,"category3":null,"image":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":1.5,"src":"/the-next-website/static/mockup002-6c1ea5a872ff241b425dc619713e2332-a7a62.jpg","srcSet":"/the-next-website/static/mockup002-6c1ea5a872ff241b425dc619713e2332-5a1a9.jpg 375w,\n/the-next-website/static/mockup002-6c1ea5a872ff241b425dc619713e2332-0908c.jpg 750w,\n/the-next-website/static/mockup002-6c1ea5a872ff241b425dc619713e2332-a7a62.jpg 1500w,\n/the-next-website/static/mockup002-6c1ea5a872ff241b425dc619713e2332-83d7d.jpg 2000w","sizes":"(max-width: 1500px) 100vw, 1500px"}}}}},"projectsInCategory":{"edges":[{"node":{"frontmatter":{"title":"9.58- Forever Fastest","graduate":"Dana Neita"}}},{"node":{"frontmatter":{"title":"Balance Magazine","graduate":"Jennifer Floto"}}}]},"projectByStudent":{"edges":[{"node":{"frontmatter":{"title":"Balance Magazine","graduate":"Jennifer Floto"}}}]}},"pathContext":{"slug":"/work/balance-magazine/","name":"/Balance Magazine/","title":"Balance Magazine","graduate":"/Jennifer Floto/","category":"/Editorial Design/"}}

